.pet-gallery
{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}
  
.pet-card
{
  border: 1px solid #ccc;
  padding: 10px;
  text-align: center;
}
  
.pet-image
{
  width: 200px; /* Resize images to 200px */
  height: auto;
  margin-top: 10px;
}

.pets-container
{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.image-container
{
  position: relative;
  perspective: 1000px;
  margin-bottom: 10px;
}

.image-card
{
  perspective: 1000px;
  width: 200px;
  height: 200px;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 1s;
}

.image-card.flipped
{
  transform: rotateY(180deg);
}

.card-front, .card-back
{
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.card-front
{
  transform: rotateY(0deg);
}

.card-back
{
  transform: rotateY(180deg);
  background-color: white;
}