.contact-us-container
{
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s ease-in-out;
}
  
.contact-us-container:hover
{
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}
  
.contact-us-container h2
{
    text-align: center;
    color: #333333;
}

@media (max-width: 768px)
{
    form
    {
      grid-template-columns: 1fr;
    }
}  
  
form
{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}
  
label
{
    font-weight: bold;
    margin-bottom: 6px;
    display: block;
}
  
input[type="text"], textarea
{
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
    resize: vertical;
}

input:focus, textarea:focus
{
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease-in-out;
}
  
label
{
    font-weight: bold;
    margin-bottom: 6px;
    display: block;
}
  
.radio-options
{
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
}

.radio-option
{
    display: flex;
    align-items: center;
    margin-bottom: 8px;
}
  
.radio-option input
{
    margin-right: 8px;
}

button
{
    background-color: #333333;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
}

button:hover
{
    background-color: #726ABC;
}
  
.error-message
{
    color: red;
    margin-top: 6px;
    font-size: 14px;
    opacity: 0;
    animation: fadeIn 0.5s ease-in-out forwards;
}
@keyframes fadeIn
{
    from
    {
      opacity: 0;
    }
    to
    {
      opacity: 1;
    }
}  